import React, { useEffect, useState, useRef } from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut, getElementAtEvent } from 'react-chartjs-2';
import ChartDataLabels from "chartjs-plugin-datalabels";
import { getConfig, getBaZhai, generateFiveYellow, generateArrow, generateGrandDuke, generateFlyingStar, generateMountain } from "../../helpers/CompassConfig"
import { TWENTY_FOUR_MOUNTAIN } from '../../cm-lib/constants/index'

ChartJS.register(ChartDataLabels);
ChartJS.register(ArcElement, Tooltip, Legend);

const color = {
    green: 'rgba(46, 204, 113, 0.3)',
    red: 'rgb( 255, 99, 132, 0)',
    gray: 'rgb(192,192,192, 0.3)'
}

const Component = ({
    children,
    degree,
    handleClick = () => { },
    labels,
    backgroundColor,
    centerText = [],
    edgeText = [],
    fiveYellow,
    facing,
    period,
    convention = '24M',
    convention2 = 'Ni',
    chartClass = {}
}) => {

    const chartRef = useRef();
    const rotation = 157.5
    const chart1Config = getConfig(convention2, {
        facing,
        isMobile: false
    })
    const chart2Config = getConfig(convention, {
        facing,
        isMobile: false
    });

    return <div className='compass'>
        <div className={chartClass?.chart1 || 'chart1'}>
            <Doughnut
                ref={chartRef}
                {...chart1Config}

            />
        </div>
        <div className={chartClass?.chart2 || 'chart2'}>
            <Doughnut
                {...chart2Config}
                onContextMenu={(event) => {
                    const dom = chartRef.current;
                    const _selected = getElementAtEvent(dom, event);
                    handleClick(event, dom, TWENTY_FOUR_MOUNTAIN[_selected[0]?.index])
                }}
                ref={chartRef}
            />
        </div>

    </div>

}

export default Component
