export const generateGrandDuke = () => {
    const bazi = dateObjectToBazi(new Date(), 'm');
    const yearBranch = bazi.GanZhiYear.ganzhi.slice(1);
    const data = mountains.map((mountain, index) => {

        const match = mountain === yearBranch
        return {
            mountain,
            backgroundColor: match ? 'brown' : 'transparent',
            label: match ? '' : ''
        }
    })
    return {
        grandDuke: {
            color: 'white',
            font: {
                size: 16,
            },
            backgroundColor: data.map(e => e.backgroundColor),
            borderRadius: 4,
            offset: 210,
            padding: 4,
            formatter: (val, ctx) => data.map(e => e.label)[ctx.dataIndex],
            align: 'end',
            anchor: 'start',
            clamp: true,
        },
    }
}


export const generateFiveYellow = () => {
    const now = new Date()
    const annualStar = getAnnualStar(now)
    const periodPlate = createBasePlate(annualStar)
    const index = periodPlate.findIndex(e => e === 5)
    const direction = getDirectionByIndex(index)

    const data = TWENTY_FOUR_MOUNTAIN.map(mountain => {
        const is5Y = mountain.facing.slice(0, -1) === direction.toLowerCase()
        return {
            backgroundColor: is5Y ? 'yellow' : 'transparent',
            label: is5Y ? '' : ''
        }
    })
    if (direction) {
        return {
            fiveYellow: {
                color: '#8B8000',
                font: {
                    size: 16,
                },
                backgroundColor: data.map(e => e.backgroundColor),
                borderRadius: 4,
                offset: 160,
                padding: 4,
                formatter: (val, ctx) => data.map(e => e.label)[ctx.dataIndex],
                align: 'end',
                anchor: 'start',
                clamp: true,
            },
        }
    }
    return {}
}

export const generateFlyingStar = (period, facing) => {

    const chart = createFlyingstarChart(facing, period)
    const data = TWENTY_FOUR_MOUNTAIN.map(mountain => {

        const match = chart.find(grid => grid.direction.toLowerCase() + '2' === mountain.facing)
        const star = match?.waterNumber + '-' + match?.baseNumber + '-' + match?.mountainNumber
        return {
            backgroundColor: !!match ? 'blue' : 'transparent',
            label: !!match ? star : ''
        }

    })
    return {
        name: {
            color: 'white',
            font: {
                size: 16,
            },
            backgroundColor: data.map(e => e.backgroundColor),
            borderRadius: 4,
            offset: 10,
            padding: 10,
            formatter: (val, ctx) => data.map(e => e.label)[ctx.dataIndex],
            align: 'end',
            anchor: 'center',
            clamp: true,
        },
    }
}

export const generateMountain = (params) => {
    const { facing } = params.facing

    const mountain = TWENTY_FOUR_MOUNTAIN.find(e => e.facing === facing)
    const data = TWENTY_FOUR_MOUNTAIN.map((m, i) => {
        let color = 'black'
        if (m.facing === mountain.facing) {

            color = 'red'
        }

        if (m.facing === mountain.sitting) {

            color = 'black'
        }
        return {
            color
        }
    })

    return {
        mountains: {
            color: data.map(e => e.color),
            font: {
                size: 24,
            },
            rotation: defaultRotation,
            offset: defaultOffset,
            borderRadius: 4,
            formatter: (val, ctx) => mountains[ctx.dataIndex] || '',
            align: 'start',
            anchor: 'end',
            clamp: true,
        },
    }
}

export const generateNi = (params) => {
    const { isMobile } = params
    const rooms = ['e2', 'se2', 'sw2', 'nw2'];
    const secondaryRooms = ['s2', 'w2', 'ne2'];
    const color = {
        green: 'rgba(46, 204, 113, 0.3)',
        red: 'rgb( 255, 99, 132, 0.3)',
        gray: 'rgb(220,220,220, 0.3)'
    }
    const data = TWENTY_FOUR_MOUNTAIN.map(e => {
        let arcColor = color.gray;
        let label = ''
        let backgroundColor = 'transparent'
        if (rooms.includes(e.facing)) {
            arcColor = color.red
            backgroundColor = 'black'
        }
        if (secondaryRooms.includes(e.facing)) {
            arcColor = color.green
            backgroundColor = 'black'
        }

        switch (e.facing) {
            case 'e2':
                label = 'Son'
                break;
            case 'se2':
                label = 'Daughter'
                break;
            case 'sw2':
                label = 'Guest Room'
                break;
            case 'nw2':
                label = 'Parent Room'
                break;
            case 's2':
                label = 'Spare Room'
                break;
            case 'w2':
                label = 'Spare Room'
                break;
            case 'ne2':
                label = 'Grandparent'
                break;
        }
        return {
            arcColor,
            label,
            backgroundColor
        }

    })

    return {
        mountains: {
            color: data.map(e => e.color),
            font: {
                size: 16,
            },
            //backgroundColor: data.map(e => e.backgroundColor),
            rotation: defaultOffset2,
            offset: defaultOffset,
            borderRadius: 4,
            formatter: (val, ctx) => data[ctx.dataIndex]?.label || '',
            align: 'start',
            anchor: 'end',
            clamp: true,
        },
    }
}

export const getBaZhai = (params) => {
    const { facing, isMobile } = params
    const color = {
        green: 'white',
        red: 'white',
        gray: 'rgb(192,192,192, 0.3)'
    }
    const chart = facing ? getBazhaiChart(facing?.facing) : null

    const sameSet = [
        ["s1", "ne2"],
        ["w2", "s3", "ne1", "se3"],
        ["sw2", "e3"],
        ["nw2", "e1"],
        ["s2", "nw1", "n1", "ne3"],
        ["n2", "n3", "sw3", "se1"],
        ["se2", "w3"],
        ["e2", "nw3", "w1", "sw1"]]

    const finalList = sameSet.reduce((prev, next) => {
        const star = chart[next[0].slice(0, -1).toUpperCase()]
        next.forEach(d => {
            prev[d] = star
        })
        return prev;
    }, {})

    console.log(finalList)

    const data = TWENTY_FOUR_MOUNTAIN.map((mountain, i) => {

        const getStar = (direction, chart) => {
            return chart ? Object.keys(chart).find(k => chart[k].toLowerCase() === direction.slice(0, -1)) : ''
        }

        const star = finalList[mountain.facing]
        let arcColor = ''
        if (["", "", "", ""].includes(star)) {
            arcColor = color.green
        } else {
            arcColor = color.red
        }
        return {
            arcColor,
            label: star
        }
    })



    return {
        config: {
            bazhai: {
                color: 'black',
                font: {
                    size: 16,
                },
                backgroundColor: isMobile ? "transparent" : 'transparent',
                borderRadius: 4,
                borderWidth: 1,
                offset: 80,

                rotation: [
                    165,
                    180,
                    195,
                    210,
                    225,
                    240,
                    255,
                    270,
                    285,
                    300,
                    315,
                    330,
                    345,
                    360,
                    15,
                    30,
                    45,
                    60,
                    75,
                    90,
                    105,
                    120,
                    135,
                    150,
                ],
                offset: [
                    -5,
                    0,
                    -5,
                    -9,
                    -12,
                    -9,
                    -3,
                    0,
                    -3,
                    -5,
                    -12,
                    -9,
                    -5,
                    0,
                    -5,
                    -9,
                    -12,
                    -9,
                    -5,
                    0,
                    -5,
                    -9,
                    -12,
                    -9,
                ],
                formatter: (val, ctx) => data.map(e => e.label)[ctx.dataIndex],
                align: 'start',
                anchor: 'center',
                clamp: true,
            },
            ...generateMountain(params),
        },
        arcColor: data.map(e => e.arcColor),
    }
}

export const generateArrow = (facing) => {
    const mountain = TWENTY_FOUR_MOUNTAIN.find(e => e.facing === facing)
    const data = TWENTY_FOUR_MOUNTAIN.map((m, i) => {
        let renderValue = ''
        let color = 'red'
        if (m.facing === mountain.facing) {
            renderValue = ''
            color = 'red'
        }

        if (m.facing === mountain.sitting) {
            renderValue = ''
            color = 'black'
        }

        return {
            value: renderValue,
            color,
            backgroundColor: renderValue ? 'white' : 'transparent'
        }
    })



    return {
        arrow: {
            color: 'red',
            font: {
                size: 24,
            },
            backgroundColor: data.map(e => e.backgroundColor),
            color: data.map(e => e.color),
            borderRadius: 4,
            offset: 42,
            padding: 2,
            formatter: (val, ctx) => data.map(e => e.value)[ctx.dataIndex],
            align: 'start',
            anchor: 'end',
            clamp: false,
        },
    }
}

