const getBaFangBaZhai = (params) => {
    const { facing, isMobile } = params
    const color = {
        green: 'rgba(46, 204, 113, 0.3)',
        red: 'rgb( 255, 99, 132, 0.3)',
        gray: 'rgb(192,192,192, 0.3)'
    }
    const chart = facing ? getBazhaiChart(facing?.facing) : null
    const data = TWENTY_FOUR_MOUNTAIN.map((mountain, i) => {

        const getStar = (direction, chart) => {
            return chart ? Object.keys(chart).find(k => chart[k].toLowerCase() === direction.slice(0, -1)) : ''
        }

        const star = getStar(mountain.facing, chart);
        let arcColor = ''
        if (["", "", "", ""].includes(star)) {
            arcColor = color.green
        } else {
            arcColor = color.red
        }
        return {
            arcColor,
            label: star
        }
    })

    const labels11 = ['N', 'NE', 'E', 'SE', 'S', 'SW', 'W', 'NW'].map(e => chart[e])

    return {
        labels: labels11,
        datasets: [{
            borderColor: 'black',
            data: [...Array(8).keys()].map(() => 100 / 8),
            backgroundColor: 'transparent',
            hoverOffset: 0,

            datalabels: {
                color: 'red',
                font: {
                    size: 16,
                },

                backgroundColor: isMobile ? "transparent" : 'transparent',
                borderRadius: 4,
                rotation: [180, 225, 270, 315, 0, 45, 90, 135],

                offset: [0, -30, 0, -30, 0, -30, 0, -30],
                padding: 10,
                formatter: (val, ctx) => labels11[ctx.dataIndex],
                align: 'start',
                anchor: 'end',
                clamp: true,
            },
        }]
    }
}

export const getBaseEightChart = (props) => {
    const { _labels, offset = 0, _color } = props
    const labels = _labels || ['', '', '', '', '', '', '', '']

    return {

        options: {
            cutout: '30%',
            borderWidth: 1,
            rotation: 157.5,
            events: ['mousemove', 'mouseout', 'click', 'touchstart', 'touchmove'],
            plugins: {
                legend: {
                    display: false
                },
                datalabels: {
                    display: true,
                    color: '#36A2EB'
                },
                tooltip: {
                    enabled: false
                }
            }
        },
        data: {
            labels: labels,
            datasets: [{
                borderColor: 'black',
                data: [...Array(8).keys()].map(() => 100 / 8),
                backgroundColor: 'transparent',
                hoverOffset: 0,

                datalabels: {
                    color: _color,
                    font: {
                        size: 16,
                    },
                    offset,
                    backgroundColor: "transparent",
                    borderRadius: 4,
                    padding: 10,
                    formatter: (val, ctx) => labels[ctx.dataIndex],
                    align: 'start',
                    anchor: 'end',
                    clamp: true,
                },

            }]
        }
    }
}

const getFlyingStarLabel = (params, _labels) => {

    const { period, direction } = params
    const { facing } = direction;
    const chart = createFlyingstarChart(facing, period)
    console.log(chart)
    const colors = _labels.map(e => e === facing ? 'red' : 'blue')
    const labels = _labels
        .map(e => chart.find(i => i.direction === e))
        .map(e => `${e?.mountainNumber} ${e?.waterNumber}`)
    return {
        labels,
        colors
    }
}


export const getConfig = (method, params) => {
    let config = '';
    const EIGHT_DIRECTION = ['N', 'NE', 'E', 'SE', 'S', 'SW', 'W', 'NW'];
    switch (method) {
        case 'BaseEightChart':
            const { direction } = params;
            const { facing } = direction;

            const colors = EIGHT_DIRECTION.map(e => e.toLowerCase() === facing.slice(0, -1) ? 'red' : 'black')
            return getBaseEightChart({
                _labels: EIGHT_DIRECTION,
                _color: colors
            })
        case 'BaFangBaZhai':
            return getBaFangBaZhai(params)
        case 'FlyingStar':
            const data = getFlyingStarLabel(params, EIGHT_DIRECTION);
            return getBaseEightChart({
                _labels: data.labels,
                offset: 70
            })
        case 'EM':
            return getBaZhai(params)
        case 'EMM':
            config = getBaZhai(params)
            return {
                config: {
                    config: config.bazhai,
                    ...generateMountain(params),
                },
                arcColor: config.arcColor,
            }
        case 'YJM':
            config = generateYiJingMethod(params);
            return {
                config: {
                    config: config.config,
                    ...generateMountain(params),

                },
                arcColor: config.arcColor,

            }
        case 'YJ':
            return generateYiJingMethod(params);
        case 'Ni':
            return generateYiJingMethod(params);
        case '24M':
            return generate24Mountain(params)
    }
}